
.slider {
    min-width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    transition: 0.5s;
}

.shadow {
    width: 100%;
    height: 100%;
    position: relative;
    background: linear-gradient(
        90deg,
        rgba(0, 0, 0, 1) 0%,
        rgba(0, 0, 0, 0) 96%
    );
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 50px;
    text-align: start;
    padding: 0 140px;
    padding-bottom: 100px;
}

.shadow::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 30%;
    background: linear-gradient(transparent, rgb(0, 0, 0));
}

.titleBox {
    padding-top: 100px;
    font-size: 3vmax;
}

.ratingBox {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}

.circleBorder {
    width: 50px; 
    height: 50px; 
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.circle {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%; 
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 16px; 
    font-weight: bold;
    color: #fff; 
    transition: background 0.5s ease-in-out; 
}


.sevo {
    width: 200px;
    color: red;
}


.infoFilmBox {
    width: auto;
    text-align: start;
}

.infoFilmBox p {
    text-align: justify;
}

.slideButtonsBox {
    height: auto;
    display: flex;
    position: relative;
    z-index: 9;
}

.watchbtn {
    padding: 8px 40px;
    border-radius: 4px;
    background-color: red;
    text-decoration: none;
    cursor: pointer;
    color: #fff;
    transition: 0.1s;
}

.watchbtn:hover {
    background-color: darkred;
}

.arrowsBtn {
    width: 100%;
    height: auto;
    position: absolute;
    top: 45%;
    left: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 60px;
    z-index: 99;
}

.arrowsBtn button {
    padding: 4px 20px;
    background-color: transparent;
    border: none;
    color: #fff;
    cursor: pointer;
    transition: 0.2s;
    border-radius: 4px;

}

.arrowsBtn button:hover {
    background-color: rgba(255, 0, 0, 0.503);
}

.arrowsBtn button svg {
    font-size: 30px;
}

@media screen and (max-width: 768px) {
    .shadow {
        padding: 0 100px;
        padding-bottom: 100px;
    }

}
@media screen and (max-width: 1024px) {
    .watchbtn:hover {
        background-color: transparent;
    }
    .shadow {
        padding: 0 50px;
        padding-bottom: 100px;
    }
    .infoFilmBox p {
        font-size: 4cqmin;
    }
    .arrowsBtn {
        justify-content: flex-end;
        bottom: -100px;
        padding: 0 30px;
        gap: 5px;
    }
    .arrowsBtn button {
        background-color: rgba(255, 0, 0, 0.203);
    }
}
